<template>
  <div class="pieCharts">
    <div id="pieCharts" ref="pieCharts"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
//这是绘制图表的参数定义
let optionTemp = {
  tooltip: {
    show: false,
    trigger: "item",
    formatter: "{a} <br/>{b} : {c} ({d}%)",
  },
  legend: {
    show: false,
    top: "bottom",
  },
  series: [
    {
      name: "",
      type: "pie",
      radius: ["50%", "75%"],
      center: ["50%", "50%"],
      //   roseType: 'radius',
      label: {
        normal: {
          // formatter: '{b}:{c}<br/> ({d}%)',
          formatter: function (params) {
            if (params.name == "轻微缺陷") {
              return `{a|${params.name}}\n${params.value} {a|${params.percent}%}`;
            } else if (params.name == "一般缺陷") {
              return `{b|${params.name}}\n${params.value} {b|${params.percent}%}`;
            } else if (params.name == "较大缺陷") {
              return `{c|${params.name}}\n${params.value} {c|${params.percent}%}`;
            } else if (params.name == "重大缺陷") {
              return `{d|${params.name}}\n${params.value} {d|${params.percent}%}`;
            }
          },
          rich: {
            //   轻微缺陷
            a: {
              color: "#439AF9",
              fontSize: "14",
              fontWeight: "700",
              lineHeight: "20",
            },
            //   一般缺陷
            b: {
              color: "#19CFDF",
              fontSize: "14",
              fontWeight: "700",
              lineHeight: "20",
            },
            //   较大缺陷
            c: {
              color: "#EDC54E",
              fontSize: "14",
              fontWeight: "700",
              lineHeight: "20",
            },
            //   重大缺陷
            d: {
              color: "#FF7556",
              fontSize: "14",
              fontWeight: "700",
              lineHeight: "20",
            },
          },
          textStyle: {
            fontWeight: "700",
            lineHeight: 14,
            fontSize: 14,
            color: "#fff",
          },
        },
      },
      itemStyle: {
        normal: {
          // borderRadius: 20,
          color: function (params) {
            let colorList = [
              {
                c1: "#439AF9", //实践
                c2: "#439AF9",
              },
              {
                c1: "#19CFDF", //操作
                c2: "#19CFDF",
              },
              {
                c1: "#EDC54E", //操作
                c2: "#EDC54E",
              },
              {
                c1: "#FF7556", //实践
                c2: "#FF7556",
              },
            ];
            return new echarts.graphic.LinearGradient(1, 0, 0, 0, [
              {
                //颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上

                offset: 0,
                color: colorList[params.dataIndex].c1,
              },
              {
                offset: 1,
                color: colorList[params.dataIndex].c2,
              },
            ]);
          },
        },
      },
      data: [
        { value: 0, name: "轻微缺陷" },
        { value: 0, name: "一般缺陷" },
        { value: 0, name: "较大缺陷" },
        { value: 0, name: "重大缺陷" },
      ],
    },
  ],
};

export default {
  name: "pieCharts",
  props: {
    index: {
      type: Number,
      default: () => {
        return null;
      },
    },
    xData: {
      /*** * xData:['一月',二月]*/
      type: Array,
      default: () => {
        return [];
      },
    },
    seriesData: {
      type: Array,
      default: () => {
        return [];
      },
    },
    //  决定x轴和y轴显示(单位)
    _zb: {
      /** :['单位','']*/
      type: Array,
      default: () => {
        return ["单位", ""];
      },
    },
    //  图表数据
    chartData: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },
  watch: {
    chartData: {
      handler() {
        this.init();
      },
      immediate: false,
      deep: true
    }
  },
  data() {
    return {
      option: optionTemp,
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    dataSet() {
      // this.option.yAxis[0].name = this._zb[0];
      // this.option.xAxis[0].name = this._zb[1];
      // this.option.xAxis[0].data = this.xData;
      // if (this.seriesData.length > 0) {
      //   this.option.legend.data = [];
      //   this.seriesData.forEach((item) => {
      //     this.option.legend.data.push(item.name);
      //   });
      //   this.option.series = this.seriesData;
      // }
      window.console.log(this.chartData, 'bug')
      this.option.series[0].data.forEach((item, index) => {
        item.value = this.chartData['level'+ (4 - index) + 'Number'] || 0 ;
      });
    },
    init() {
      this.dataSet();
      let mychart = echarts.init(this.$refs.pieCharts);
      window.addEventListener("resize", mychart.resize);
      //将会绘制图表的参数加到图表中
      
      mychart.setOption(this.option);
      let $this = this;
      mychart.on("click", function (params) {
        $this.$emit("getData", params, $this.index);
      });
    },
  },
};
</script>

<style scoped lang="scss">
.pieCharts {
  height: calc(100% - 56px);
  #pieCharts {
    height: 100%;
  }
}
</style>